@import "./fonts-and-variables.scss";

.container-general{
    display: flex;
    flex-wrap: wrap;
    height: 100%;   
    width: 100%;
    animation-name: show-from-right;
    animation-duration: 0.3s;

    .synopsis{
        width: 75%;

        .synopsis-text{
            padding-top: 20px;
            padding-left: 30px;
            color:$commom-font-color;
            font-family: Nunito-Light;
            width: 100%;
            font-size: 16px;

            .synopsis-title{
                font-size: 18px;
            }
        }
    }

    .icons{
        padding-top: 30px;
        display: flex;
        flex-wrap: wrap;
        width: 25%;
        height: 100%;

        .icon{
            width: 25%;
            height: 100%;
            cursor: pointer;

            .icon-image{
                margin-left: calc(50% - 17px);
                margin-bottom: 5px;
                margin-top: 5px;
            }

            .icon-text{
                color:$commom-font-color;
                font-family: Nunito-Light;
                text-align: center;
            }

            &:hover{
                opacity: 0.7;
            }
        }
    }
}

@media screen and (max-width: 1180px){
    .container-general{
        .synopsis{
            width: calc(60% - 40px);
        }
    
        .icons{
            width: 40%;
        }
    }
}

@media screen and (max-width: 768px){
    .container-general{
        .synopsis{
            width: calc(100% - 40px);
            margin: 20px;
            .synopsis-text{
                padding-top: 0px;
                padding-left: 0px;
    
            }
        }
    
        .icons{
            justify-content: center;
            width: 100%;
        }
    }
}