@import "./fonts-and-variables.scss";

.season-tab, .season-tab-selected{
    font-family:Nunito-Light;
    width: 40px;
    text-align: center;
    padding: 5px 10px;
    font-size: 16px;                  
}

.season-tab{
    color:$commom-font-color;
    cursor:pointer;
    border-bottom: 3px solid $light-gray;

    &:hover{
        color:$white;
        border-bottom: 3px solid $green;
    }
}

.season-tab-selected{
    color:$white;
    cursor: default;
    border-bottom: 3px solid $light-green;
}