@import "./fonts-and-variables.scss";

.episode-container{
    min-height: 70px;
    border-bottom: 1px solid $light-gray;
    position: relative;

    .episode-title{
        padding-top: 24px; 
        padding-left: 5px;
        color:$commom-font-color;
        font-family: Nunito-Regular;
        font-size: 18px;    
        cursor: pointer;

        .play-button{
            position: absolute;
            top: 15px;
            right: 5px;
        }   

        &:hover{
            opacity: 0.8;

            .play-button{
                opacity: 0.6;
            }
        }
    }

    .ep-data{
        margin-top: 25px;
        width: 85%;
        margin: 30px 15px;
        overflow: hidden;
        animation-name: show-from-above;
        animation-duration: 1s;

        .ep-thumbnail{
            width: 100%;
        }

        .ep-synopsis{
            color:$commom-font-color;
            font-family: Nunito-Regular;
            font-size: 16px;  
            margin-top: 10px;
        }
    }
}

@media screen and (max-width: 768px){
    .episode-container{
        .episode-title{
            padding-left: 20px;
            max-width: calc(100% - 130px);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;

            .play-button{
                right: 40px;
            }
        }
    }
}