@import "./fonts-and-variables.scss";

.container-cast{
    overflow: hidden;
    animation-name: show-from-right;
    animation-duration: 0.3s;

    .cast{
        display: flex;
        flex-wrap: wrap;
        width: 2000px;

        .cast-member{
            color: $commom-font-color;
            width: 240px;
            height: 100px;
            margin-right: 10px;
            margin-top: 25px;
            background-color: $light-gray;
        
            .member-name{
                text-align: center;
                font-family: Nunito-Light;
                font-size: 22px;
                margin-top: calc(50px - 14px);
            }
        }
    }
}

@media screen and (max-width: 768px){
    .container-cast{      
        overflow: none;

        .cast{
            width: 100%;
            margin-top: 20px;
            margin-bottom: 20px;

            .cast-member{
                width: calc(100% - 40px);
                margin: 5px 20px;
            }
        }
    }
}