@import "./fonts-and-variables.scss";

.data-area{
    min-height: 250px;
    max-height: 250px;
    width: 100%;
    position: relative;
    background-color: $gray;
    overflow: hidden;

    .tabs{
        display: flex;
        flex-wrap: wrap;
        padding-top: 40px;
        margin: 0px 100px;

        .tab, .tab-selected{
            font-family:Nunito-Light;
            width: 100px;
            text-align: center;
            padding: 5px 10px;
            font-size: 20px;                   
        }

        .tab{
            color:$commom-font-color;
            cursor:pointer;
            border-bottom: 3px solid $light-gray;

            &:hover{
                color:$white;
                border-bottom: 3px solid $green;
            }
        }

        .tab-selected{
            color:$white;
            cursor: default;
            border-bottom: 3px solid $light-green;
        }
    }

    .divider{
        height: 3px;
        width: calc(100% - 200px);
        margin: -3px 100px 10px 100px;
        background-color: $light-gray;
    }

    .telecine-img{
        position: absolute;
        top: 35px;
        right: 100px;
    }

    .tab-content{
        width: calc(100% - 200px);
        margin: 10px 100px;
        height: calc(100% - 123px);
    }
}

@media screen and (max-width: 768px){
    .data-area{
        min-height: 250px;
        max-height: 1000px;

        .tabs{
            margin: 0px;
        }

        .divider{
            width: 100%;
            margin: 0px;
            margin-top: -3px;
        }

        .telecine-img{
            right: 10px;
        }

        .tab-content{
            width: 100%;
            margin: 0px;
        }
    }
}