@import "./fonts-and-variables.scss";

.container-seasons{
    height: calc(100% - 400px);
    width: 30%;
    position: absolute;
    right: 40px;
    top: 80px;
    z-index: 9999;
    overflow: hidden;

    .season-tabs{
        display: flex;
        flex-wrap: wrap;   
    }

    .divider{
        height: 3px;
        margin-top: -3px;
        background-color: $light-gray;
    }

    .episodes{
        min-height: 0%;
        max-height: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        width: calc(100% + 20px);
    }
}

@media screen and (max-width: 768px){
    .container-seasons{
        background-color: $gray;
        position: relative;
        width: 100%;
        right: 0;
        left: 0;
        top: 0;
        height: calc(100% - 200px);
        .season-tabs{
            margin-top: 10px;
        }

        .episodes{
            min-height: 100px;
            max-height: 2000px;
        }
    }   
}