@import "./fonts-and-variables.scss";

.title-area{  
    width: 50%;
    position: absolute;
    top: 30px;
    left: 60px;
    z-index: 200;

    .title{
        font-family: Nunito-Bold;
        font-size: 48px;
        color:$white;
        height: 60px;
    }

    .title-info{
        font-family: Nunito-ELight;
        font-size: 20px;
        color:$commom-font-color;
    }
}

@media screen and (max-width: 768px){
    .title-area{  
        width: 80%;
        top: 10px;
        left: 10px;

        .title{
            font-size: 38px;
            height: 40px;
        }

        .title-info{
            font-size: 18px;
            color: $white;
        }
    }
}