@import "./fonts-and-variables.scss";

body{
    margin: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; 

    .page-container{
        height: 100vh;
        .background{
            height: calc(100% - 250px);
            width: 100%;
            overflow: hidden; 
            position: relative;   
            background-color: $black;

            .background-image{
                width: 100%;
                margin-left: -200px               
            }

            .image-shadow{
                z-index: 2;
                position: absolute;
                top: 0;
                left: 0;
                width: 90%;
                height: 100%;
                background-image: radial-gradient(transparent, $black);

                &::after{
                    content: '';
                    width: 45%;
                    height: 100%;
                    position: absolute;
                    right: 0;
                    background-image: -webkit-linear-gradient(left, $transparent-black, $black, $black);
                    background-image: linear-gradient(to right, $transparent-black, $black, $black);           
                }

                &::before{
                    content: '';
                    width: 100%;
                    height: 10%;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    background-image: -webkit-linear-gradient(top, $transparent-black, $black);
                    background-image: linear-gradient(to bottom, $transparent-black, $black);           
                }
            }          
        }

        .close-page-icon{
            font-size: 24px;
            font-family: Nunito-Bold;
            color: $commom-font-color;
            position: absolute;
            top: 20px;
            right: 20px;
            cursor: pointer;
            z-index: 8888;

            &:hover{
                opacity: 0.6;
            }
        }

        .tv-show{
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
        }
    }
}

@media screen and (max-width: 1180px){
    body{  
        .page-container{
            .background{
                .background-image{
                    height: 100%;
                }
            }
        }
    }
}

@media screen and (max-width: 768px){
    body{  
        .page-container{
            .background{
                height: initial;
                .background-image{
                    width:100%;
                    margin-left: 0px;
                }
                .image-shadow{
                    width: 100%;
    
                    &::after{
                        display: none;
                    }
                }
            }
        }
    }
}